.banner_content {
  color: whitesmoke;
  object-fit: contain;
  height: 400px;
}

.banner_contain {
  padding-top: 100px;
  margin-left: 40px;
  height: 180px;
}
.banner_head {
  font-size: 3rem;
  font-weight: 700;
  padding-bottom: 0.4rem;
}

.banner_desc {
  width: 65rem;
  line-height: 1.3rem;
  padding-top: 1rem;
  font-size: 0.8rem;
  max-width: 350px;
  text-align: justify;
}

.btnbanner {
  cursor: pointer;
  color: whitesmoke;
  outline: none;
  border: none;
  border-radius: 0.2vw;
  padding-left: 2rem;
  padding-right: 2rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  margin-right: 1rem;
  background-color: rgba(51, 51, 51, 0.5);
  font-weight: 600;
}

.btnbanner:hover {
  transition: all 0.2s;
  background-color: #e6e6e6;
  color: black;
}
.bannerbottm {
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(35, 35, 35, 0.6),
    #111
  );
  height: 7.6rem;
}
