.row {
  color: whitesmoke;
  margin-left: 15px;
}

.movie_poster {
  width: 100%;
  object-fit: contain;
  max-height: 100px;
  margin-right: 8px;
  transition: tranform 450ms;
  cursor: pointer;
}

.movie_poster:hover {
  transform: scale(1.08);
}
.poster_images {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 15px;
}
.poster_images::-webkit-scrollbar {
  display: none;
}

.posterlarge {
  max-height: 200px;
}
.posterlarge:hover {
  transform: scale(1.09);
}
